
.NewTodoForm .nes-input{
    width: 17rem;
    font-size: 0.75rem;
    margin: 1rem 0;
    background: #cccc81;
    border-image-source: url('data:image/svg+xml;utf8,<?xml version="1.0" encoding="UTF-8" ?><svg version="1.1" width="5" height="5" xmlns="http://www.w3.org/2000/svg"><path d="M2 1 h1 v1 h-1 z M1 2 h1 v1 h-1 z M3 2 h1 v1 h-1 z M2 3 h1 v1 h-1 z" fill="rgb(46, 43, 18)" /></svg>');
}

.NewTodoForm .nes-input::placeholder{
    color: #2e2b1260;
}

.NewTodoForm .nes-btn{
    font-size: 0.75rem;
    border-image-source: url('data:image/svg+xml;utf8,<?xml version="1.0" encoding="UTF-8" ?><svg version="1.1" width="5" height="5" xmlns="http://www.w3.org/2000/svg"><path d="M2 1 h1 v1 h-1 z M1 2 h1 v1 h-1 z M3 2 h1 v1 h-1 z M2 3 h1 v1 h-1 z" fill="rgb(51, 60, 12)" /></svg>');
}


.NewTodoForm .nes-btn.is-success{
    color: #405010;
    background-color: #D0D058;
}

.NewTodoForm .nes-btn.is-error{
    background-color: #405010;
}

.NewTodoForm .nes-btn.is-error:after{
    box-shadow: inset -4px -4px #2c360e;
}

.NewTodoForm .nes-btn.is-success::after {
    position: absolute;
    top: -4px;
    right: -4px;
    bottom: -4px;
    left: -4px;
    content: "";
    box-shadow: inset -4px -4px #708028;
}

.nes-btn.is-success:active:not(.is-disabled)::after {
    box-shadow: inset 4px 4px #708028;
}

.nes-btn.is-success:focus {
    box-shadow: 0 0 0 6px #4b5920;
}

.NewTodoForm .nes-btn.is-success:hover {
    /* color: #fff; */
    text-decoration: none;
    background-color: #a8a844
}

.NewTodoForm .float{
	position:fixed;
	bottom:40px;
    right:40px;
    z-index: 999;
}
 
.NewTodoForm .my-float{
	margin-top:22px;
}

.NewTodoForm #dialog-rounded {
    display: block;
    z-index: 999;
    background: #D0D058;
}
