.Todo .Todo-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 3rem;
}

.Todo .Todo-item .Todo-btns button{
    margin: 0rem 1rem;
}

.crossline{
    text-decoration-line: line-through;
    text-decoration-color: #405010;
    color: #40501067;
    transition: all 3s ease;
}

.Todo .dd{
    position: relative;
    cursor: url('data:image/png;base64,') 14 0, pointer;
}

.Todo .dd-input{
    display: none;
}

.Todo .dd-menu{
    display: none;
}

.Todo .nes-container {
    background-color: #d0d058;
    color: #405010;
    
}

.Todo .nes-container p{
    padding-bottom: 0.75rem;
}

.Todo .nes-container p:nth-child(1):hover{
    color: #036007;
}

.Todo .nes-container p:nth-child(2):hover{
    color: #965a03;
}

.Todo .nes-container p:nth-child(3):hover{
    color: #a81005;
}

.Todo .dd-input:checked + .dd-menu{
    display: block;
    position: absolute;
    top: 52px;
    left: 20px;
    z-index: 999;
    font-size: 0.75rem;
    padding: 0.75rem;
}